.banner, 
.aboutBanner {
    position: relative;
    border-radius: 25px;
    max-width: 1240px;
    width: 100%;
    height: 223px;
    object-fit: cover;
    margin: 0px auto;
    background-color: rgba(0, 0, 0, 0.2);
    mix-blend-mode: multiply;
}

.banner-title {
    position: absolute;
    width: 600px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 40px;
    text-align: center;
    font-weight: 400;
    margin:0px;
    @media (max-width: 1240px) {
        flex-direction: column;
        max-width: 100%;
        font-size: 25px;
    }
}

