.card {
    position: relative;
    flex-direction: column;
    align-items: flex-start;
    height: 330px;
    width: 330px;
    border-radius: 10px;
    background: #FF6060;
    padding: 10px;
    margin: 50px 25px 50px 25px;
    cursor:pointer;
}

.card img {
    object-fit: cover;
    width: 100%;
    height: 85%;
    border-radius: 10px;
    z-index: 0;
}

.card h3 {
    color: #FFFFFF;
    margin: 5px;
    font-weight: 500;
    font-size: 18px;
}
a {
    text-decoration: none;
}
